
.navbar-brand {
    font-family: 'Ubuntu', sans-serif;
    font-size: 2rem;
    font-weight: 400;
    padding-left: 2rem;
}

.nav-item {
    padding: 0 1rem;
    background-color: #065b7d;
}

.nav-link {
    font-family: 'Montserrat', sans-serif;
    font-weight: 200;
    font-size: 1.2rem;
}

.navbar {
    width: 100%;
    background-color: #065b7d;
    height: 6rem;
    display: flex;
    flex-direction: row;
    justify-items: center;
    align-items: center;
}

.navbar-nav {
    display: flex;
    flex-direction: row;
    justify-items: center;
    align-items: flex-end;
    padding-right: 2rem;
    width: 100vh;
}