#footer {
    padding: 8% 16%;
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url("../imgs/footer.jpg");
    height: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

}

.footerimg{
    display: flex;
    flex-direction: row;
    justify-content: center;
    
}


.footer-text {
    color: white;
    text-align: center;
}

.social-icon {
    padding: 1rem 1rem;
    color: white;

}

.social-icon:hover {
    color: #0AA1DD;
}

.white-section {
    background-color: #fff;
    color: #112B3C;
}