#slider {
    background-color: #000000;
    color: #fff;
    padding-top: 1rem;
    width: 100%;
}

.carousel-item {
    padding: 8% 16%;
    
}

.carousel-item p{
    text-align: justify;
}

.slider-text {
    font-size: 3rem;
    line-height: 1.5;
    text-align: center;
    
}

.slider-img img {
    width: 75%;
    min-width: 300px;
    height: auto;
    padding-bottom: 1rem;
    border-radius: 2.5%;
}

.slider-img{
    display: flex;
    justify-content: center;
}



.colored-section {
    background-color: #065b7d;
    color: #fff;
    

}