#pillars .row {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    grid-template-columns: repeat(2, 1fr);
    justify-items: center;
    align-items: center;
    grid-auto-flow: row;
    width: 100%;
    margin: 0rem;

}

.element {
    width: 75%;
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

#pillars {
    padding: 5% 0.5rem;

}

#pillars .row i:hover {
    color: #112B3C;
}

.colored-section {
    background-color: #065b7d;
    color: #fff;
    

}